html {
  background: #efefef; }

a {
  color: #3f3f3f; }
  a:hover {
    color: #555555; }

.section {
  background-color: #ffffff !important; }

.invisible-onload {
  transition: all 0.4s;
  opacity: 0; }
  .invisible-onload.loaded {
    opacity: 1; }

.btn-default {
  background-color: #ffffff; }
  .btn-default:hover {
    background-color: #efefef; }

.search-box .btn-default {
  border: 1px solid #ced4da; }

.rack-board {
  display: flex; }
  .rack-board > * {
    align-self: center; }
  .rack-board > span {
    padding-top: 0.01em; }

#header .navbar {
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.24); }

.link {
  color: #3f3f3f; }
  .link:hover {
    color: #555555; }

.animatable {
  transition: all 0.25s; }

.touchable {
  transition: all 0.25s;
  opacity: 0.75; }
  .touchable:hover {
    opacity: 1; }
  .touchable:active {
    opacity: 1; }

.attractive:hover {
  transform: scale(1.14); }

.arrow {
  background-image: url('data:image/svg+xml;utf8,<svg viewBox="0 0 300 200"> <use xlink:href="#circle"></use> </svg>');
  background-repeat: no-repeat;
  background-size: cover; }

.arrow-0deg {
  transform: rotate(0deg); }

.arrow-90deg {
  transform: rotate(90deg); }

.arrow-180deg {
  transform: rotate(180deg); }

.arrow-270deg {
  transform: rotate(270deg); }

.article-list-group .article-list .badge-new {
  color: #ffffff;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background-color: #ce6f3a; }

.article-list-group .article-list-column-caption {
  color: #333; }

.comment-list-area {
  margin-bottom: 1.8em; }

.comment-list {
  padding: 0;
  list-style: none; }
  .comment-list > .comment {
    border-bottom: 1px solid #efefef; }
  .comment-list .children {
    border-top: 1px dashed #dfdfdf; }
  .comment-list ol {
    list-style: none; }
  .comment-list .comment-body {
    padding-top: 1em;
    padding-bottom: 1em; }
    .comment-list .comment-body .comment-author img {
      width: 25%;
      max-width: 60px;
      float: left;
      margin-right: 1.2em;
      border-radius: 0.3em; }
    .comment-list .comment-body .comment-author .fn {
      font-size: 1.1em; }
    .comment-list .comment-body .comment-author .says {
      display: none; }
    .comment-list .comment-body .comment-meta a {
      color: #aaa;
      font-size: 0.9em; }
    .comment-list .comment-body > p {
      clear: both;
      padding-top: 1.4em; }

.comment-form-comment textarea {
  width: 100%;
  border-radius: 0.3em;
  border: 1px solid #ddd;
  padding: 0.4em 0.6em; }

.related-categories {
  list-style: none;
  padding: 0.4em;
  margin-bottom: 0; }
  .related-categories li a {
    padding: 0.4em 0.6em;
    border: 1px solid #ccc; }

.gototop {
  padding: 0.4em 0.6em;
  margin-top: 2em;
  display: inline-block; }
  .gototop:hover {
    opacity: 1;
    cursor: pointer;
    text-decoration: none; }
